import { Component, OnInit } from '@angular/core';
import { AuthService } from 'src/app/auth/auth.service';
import { isArray } from 'util';
import { FetchpackagesService } from '../../services/fetchpackages.service';

@Component({
  selector: 'app-myplans',
  templateUrl: './myplans.component.html',
  styleUrls: ['./myplans.component.css'],
})
export class MyplansComponent implements OnInit {
  constructor(
    private fetchpackagesservice: FetchpackagesService,
    private authservice: AuthService
  ) {}
  packages: any;
  leisurepacks: any = [];
  eventpacks: any = [];
  businesspacks: any = [];
  event: any = [];
  business: any = [];
  user = this.authservice.getUsername();

  checkifEventExist(name: any): boolean {
    for (var i in this.event) {
      if (this.event[i].name == name) return true;
    }
    return false;
  }
  addToExistingEvent(name: any, pack: any) {
    for (var i in this.event) {
      if (this.event[i].name == name) {
        if (isArray(this.event[i].package) != true) {
          this.event[i].package = [this.event[i].package];
        }
        this.event[i].package.push(pack);
      }
    }
  }

  checkifBusinessExist(name: any): boolean {
    for (var i in this.business) {
      if (this.business[i].name == name) return true;
    }
    return false;
  }

  addToExistingBusiness(name: any, pack: any) {
    for (var i in this.business) {
      if (this.business[i].name == name) {
        if (isArray(this.business[i].package) != true) {
          this.business[i].package = [this.business[i].package];
        }
        this.business[i].package.push(pack);
      }
    }
  }

  ngOnInit(): void {
    this.fetchpackagesservice
      .userPackages(this.user.email)
      .subscribe((data) => {
        this.packages = data;
        for (var i in this.packages) {
          if (this.packages[i].packageType == 'leisure')
            this.leisurepacks.push(this.packages[i]);
          else if (this.packages[i].packageType == 'Business')
            this.businesspacks.push(this.packages[i]);
          else this.eventpacks.push(this.packages[i]);
        }

        this.leisurepacks.map((v: any) => {
          v.package = JSON.parse(v.package);
          v.package.startDate = v.package.startDate.substr(0, 10);
          if (v.package.endDate)
            v.package.endDate = v.package.endDate.substr(0, 10);
        });
        this.businesspacks.map((v: any) => {
          v.bookedon = v.bookedon.substr(0, 10);
          v.package = JSON.parse(v.package);
          v.package.startDate = v.package.startDate.substr(0, 10);
          if (v.package.endDate)
            v.package.endDate = v.package.endDate.substr(0, 10);
        });
        this.eventpacks.map((v: any) => {
          v.bookedon = v.bookedon.substr(0, 10);
          v.package = JSON.parse(v.package);
          v.package.startDate = v.package.startDate.substr(0, 10);
          if (v.package.endDate)
            v.package.endDate = v.package.endDate.substr(0, 10);
        });

        for (var i in this.eventpacks) {
          if (this.checkifEventExist(this.eventpacks[i].name)) {
            this.addToExistingEvent(
              this.eventpacks[i].name,
              this.eventpacks[i].package
            );
          } else {
            this.eventpacks[i].package = [this.eventpacks[i].package];
            this.event.push(this.eventpacks[i]);
          }
        }

        for (var i in this.businesspacks) {
          if (this.checkifBusinessExist(this.businesspacks[i].name)) {
            this.addToExistingBusiness(
              this.businesspacks[i].name,
              this.businesspacks[i].package
            );
          } else {
            this.businesspacks[i].package = [this.businesspacks[i].package];
            this.business.push(this.businesspacks[i]);
          }
        }
      });
  }
}
