import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../../../auth/auth.service';
import { NavigationEnd, Router, RoutesRecognized } from '@angular/router';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css'],
})
export class DashboardComponent implements OnInit {
  constructor(private authservice: AuthService, private router: Router) {
    this.router.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        document.getElementById('home')?.classList.remove('active');
        document.getElementById('personalinfo')?.classList.remove('active');
        document.getElementById('packages')?.classList.remove('active');
        document.getElementById('payments')?.classList.remove('active');
        document.getElementById('security')?.classList.remove('active');
        document.getElementById('others')?.classList.remove('active');
        document.getElementById('home1')?.classList.remove('active');
        document.getElementById('personalinfo1')?.classList.remove('active');
        document.getElementById('packages1')?.classList.remove('active');
        document.getElementById('payments1')?.classList.remove('active');
        document.getElementById('security1')?.classList.remove('active');
        if (event.url == '/user') {
          document.getElementById('home')!.className += ' active';
          document.getElementById('home1')!.className += ' active';
        } else if (event.url == '/user/your_packages') {
          document.getElementById('packages')!.className += ' active';
          document.getElementById('packages1')!.className += ' active';
        } else if (event.url == '/user/profile') {
          document.getElementById('personalinfo')!.className += ' active';
          document.getElementById('personalinfo1')!.className += ' active';
        } else if (event.url == '/user/your_payments') {
          document.getElementById('payments')!.className += ' active';
          document.getElementById('payments1')!.className += ' active';
        } else if (event.url == '/user/security') {
          document.getElementById('security')!.className += ' active';
          document.getElementById('security1')!.className += ' active';
        }
      }
    });
  }

  ngOnInit(): void {}

  signout() {
    this.authservice.logout();
    this.router.navigate(['/login']);
  }
}
